import React from 'react';

class ToDoForm extends React.Component {
	// constructor(props) {
	// 	super(props);

	// 	this.handleSubmit = this.handleSubmit.bind(this);
	// }

	// if you comment the above constructor out, and make the handleSubmit,  in line 13, into a function it does the bind thing.
	textInput = React.createRef();

	handleSubmit = (e) => {
		e.preventDefault(); // to prevent the page from refreshing
		const text = this.textInput.current.value.trim();
		this.props.addToDo(text);
		e.currentTarget.reset();
		// console.log(`create new item: ${this.textInput.current.value}`);
	};
	render() {
		return (
			<form className="input-group my-3" onSubmit={this.handleSubmit}>
				<input className="form-control" type="text" placeholder="Add a new todo item..." ref={this.textInput} />
				<div className="input-group-append">
					<button className="btn btn-outline-secondary" type="submit">
						<i className="fas fa-plus" aria-hidden="true" />
						&nbsp;Add Item
						{/* the &nbsp; adds a space before add item */}
					</button>
				</div>
			</form>
		);
	}
}

export default ToDoForm;
